import './just-validate.min.js'
import './inputmask.min.js'
document.addEventListener('DOMContentLoaded', function() {
    document.querySelector('.product__button').addEventListener('click', function() {

        document.querySelector('.form').classList.add('form-active')
    
    })
    
    document.querySelector('.form__close-button').addEventListener('click', function() {
    
        document.querySelector('.form').classList.remove('form-active')
    
    })
    
    let popup = document.querySelector('.form__wrapper');
    document.addEventListener('mousedown', function(e){
        if(e.target.closest('.form__wrapper') === null){
            document.querySelector('.form').classList.remove('form-active');
        }
    });
    
    const selector = document.querySelector('.form__tel');
    
    let im = new Inputmask("+7 (999)-999-99-99");
    
    im.mask(selector);
    
    new JustValidate('.form__body', {
        rules: {
            name: {
                required: true,
                minLength: 2,
                maxLength: 15
            },
            tel: {
                required: true,
                function: (name, value) => {
                    const phone = selector.inputmask.unmaskedvalue()
                    return Number(phone) && phone.length === 10
                }
            },
        },
    });
})




