document.addEventListener('DOMContentLoaded', function() {
  document.querySelectorAll('.tab-button').forEach(function(stepBtn) {
    stepBtn.addEventListener('click', function(event) {
      
      const path = event.currentTarget.dataset.path;

      document.querySelectorAll('.tab-content').forEach(function(stepContent) {
        stepContent.classList.remove('content-active');

        document.querySelector(`[data-target="${path}"]`).classList.add('content-active');
        document.querySelector(`[data-path="${path}"]`).classList.add('button-active');
      });

      document.querySelectorAll('.tab-button').forEach(function(stepContent) {
        stepContent.classList.remove('button-active');

        document.querySelector(`[data-path="${path}"]`).classList.add('button-active');
      });
    });   
  });
});


